
<template>

    <div>
        <el-tag type="warning"  style="margin-left: 20px;margin-bottom: 10px;width: 200px;height: 40px;text-align: center;line-height: 40px;font-size: 20px">供应商配置</el-tag>
        <el-col :span="23" style=" padding-bottom: 30px;">

            <el-row>

                <el-col :span="1.5" class="el-col" style="">
                    <p class="input-title">&nbsp;</p>
                    <el-button type="primary" icon="el-icon-plus"  @click = "addruntime">新增</el-button>
                </el-col>
            </el-row>
        </el-col>


        <el-col :span="23" style=" padding-top: 30px;">
            <table class="content-table">
                <tr>
                    <th>ID</th>
                    <th>名称</th>

                    <th>操作</th>


                </tr>
                <tr v-for="(item,index) in aaData">
                    <td>{{index+1}}</td>

                    <td>{{item.city}}</td>
                    <td>
                        <span class="dothis">编辑</span>
                        <span class="dothis">删除</span>
                    </td>
                </tr>
            </table>
            <div class="block page-f" style="position: relative;width: 100%">

                <el-pagination
                    style="position: absolute;right: 0"
                    background
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page.sync="currentPage"
                    :page-size="pageSize"
                    :page-sizes="[10, 20, 30, 40]"
                    layout="total,sizes,prev, pager, next, jumper"
                    :total="totalNum">
                </el-pagination>
            </div>
        </el-col>

        <el-dialog title="新增行程" :visible.sync="addvisible">
            <el-form :model="addForm">
                <el-form-item label="出发地" :label-width="formLabelWidth">
                    <el-input v-model="addForm.depStation" auto-complete="off" style="width: 400px" placeholder="请输入出发地"></el-input>
                </el-form-item>
                <el-form-item label="到达地" :label-width="formLabelWidth">
                    <el-input v-model="addForm.destStation" auto-complete="off " style="width: 400px" placeholder="请输入到达地"></el-input>
                </el-form-item>
                <el-form-item label="耗时" :label-width="formLabelWidth" >
                    <el-input type="number" v-model="addForm.hours" auto-complete="off " style="width: 150px" placeholder="请输入小时数"></el-input>

                    时
                    &nbsp; &nbsp; &nbsp;
                    <el-input type="number" v-model="addForm.minutes" auto-complete="off " style="width: 150px" placeholder="请输入分钟数"></el-input>
                    分
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="addvisible = false">取 消</el-button>
                <el-button type="primary" @click="addsure" >确 定</el-button>
            </div>
        </el-dialog>

        <el-dialog title="编辑行程" :visible.sync="addvisible">
            <el-form :model="editForm">
                <el-form-item label="出发地" :label-width="formLabelWidth">
                    <el-input v-model="editForm.depStation" auto-complete="off" style="width: 400px" placeholder="请输入出发地"></el-input>
                </el-form-item>
                <el-form-item label="到达地" :label-width="formLabelWidth">
                    <el-input v-model="editForm.destStation" auto-complete="off " style="width: 400px" placeholder="请输入到达地"></el-input>
                </el-form-item>
                <el-form-item label="耗时" :label-width="formLabelWidth" >
                    <el-input type="number" v-model="editForm.hours" auto-complete="off " style="width: 150px" placeholder="请输入小时数"></el-input>

                    时
                    &nbsp; &nbsp; &nbsp;
                    <el-input type="number" v-model="editForm.minutes" auto-complete="off " style="width: 150px" placeholder="请输入分钟数"></el-input>
                    分
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="addvisible = false">取 消</el-button>
                <el-button type="primary" @click="addsure" >确 定</el-button>
            </div>
        </el-dialog>
    </div>
</template>
<script>
    import ajax from "../model/sys/index"
    export default {
        data() {
            return {
                value:'',
                currentPage: 1,//默认页码
                pageSize:10,//每页显示条数
                totalNum:10,//总条数
                aaData:[],
                addvisible:false,
                from:"",
                to:"",
                searchForm:{
                    depStation:"",
                    destStation:""
                },
                addForm: {
                    depStation:"",
                    destStation:"",
                    hours:"",
                    minutes:"",
                },
                editForm: {
                    depStation:"",
                    destStation:"",
                    hours:"",
                    minutes:"",
                    id:""
                },
                formLabelWidth: '120px',

            };
        },
        methods: {


            getRunTimeList:function () {
                var data  = this.searchForm
                data.pageStartIndex=this.currentPage
                data.pageRowsCount=this.pageSize

                for(var i in data){
                    if(String(data[i])==""){
                        delete data[i]
                    }
                }

                ajax.runTimeList(data,res=>{
                    this.aaData = res.data.orders
                    this.totalNum = res.data.totalCount
                    this.fullscreenLoading = false
                })
            },

            addruntime(){
                this.addvisible = true
                for(var i in this.addForm){
                    this.addForm[i]=""
                }
            },
            addsure(){
                ajax.runTimeAdd(this.addForm,res=>{
                    console.log(res)
                })
            },

            clear:function () {
                this.checkedCities=[]
                this.checkAll=false
                var data  = this.searchForm
                for(var i in data){
                    data[i]=""
                }
                this.value1=""
                this.value2=""

            },
            search:function () {

                console.log(this.checkedCities)
                return
                this.fullscreenLoading = true
                this.currentPage = 1
                this.getOrderList ()
                setTimeout(() => {
                    this.fullscreenLoading = false;
                }, 2000);
            },
            orderExport:function () {
                var orderData  = this.searchForm
                for(var i in orderData){
                    if(String(orderData[i])==""){
                        delete orderData[i]
                    }
                }
                ajax.orderExport(orderData)
            },
            addDevice:function () {
                this.dialogFormVisible = true
            },
            getTimeValue1:function (val) {
                console.log(val)
                if(val==null){
                    this.searchForm.createFromDate = ""
                }else {
                    this.searchForm.createFromDate = val
                }
            },
            getTimeValue2:function (val) {
                console.log(val)
                if(val==null){
                    this.searchForm.createToDate = ""
                }else {
                    this.searchForm.createToDate = val
                }
            },
            goorderdetail(serial){
                window.open("#/coin_order_detail?serial="+serial)
            },
            refund:function (data) {
                var refundData= {
                    serial:data
                }
                this.$confirm('是否继续退款操作?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    ajax.refund(refundData,res=>{
                        if(res){
                            this.getOrderList()
                            this.$message({
                                showClose: true,
                                message: '退款成功',
                                type: 'success'
                            });
                        }
                    })
                }).catch(() => {

                });

            },
            handleSizeChange(val) {
                this.pageSize = val
                this.getOrderList ()
            },
            handleCurrentChange(val) {
                this.currentPage = val
                this.getOrderList ()
            }
        },
        mounted(){

//            ajax.getResource({key:"SubwayCity,CoinOrderStatus,CoinOrderSource,OneYuanBackStatus"},res=>{
//                this.cityOptions = res.data.data.SubwayCity
//                this.CoinOrderStatus = res.data.data.CoinOrderStatus
//                this.CoinOrderSource = res.data.data.CoinOrderSource
//                this.OneYuanBackStatus = res.data.data.OneYuanBackStatus
//                this.getOrderList()
//            })
         }
    }
</script>
<style>
    .input-title{
        font-size: 14px !important;
        font-weight: 600;
    }
    .el-col{
        margin-left: 20px;
    }
    ::-webkit-scrollbar-thumb{
        background-color: gainsboro;
    }
    .cell{
        text-align: center;
    }
    #content-main{
        overflow: scroll;
    }
    .el-table{
        overflow: scroll;
    }
    .content-table{
        width: 100%;
        border-collapse:collapse;
        background-color: white;
        text-align: center;
    }
    .content-table th{
        height: 40px;
        border: 1px solid #F2F3F4;
        text-align: center;
        background-color: gainsboro;
    }
    .content-table td{
        height: 40px;
        border: 1px solid #F2F3F4;
    }
    .dothis{
        color: #3C9CFC;
        cursor:default;
        margin-left: 10px;
    }
    .page-f{
        margin-top: 20px;
        float: right;
    }
    .content-table span:hover{
        color: silver;
    }
    .el-col-23{
        width: 99% !important;
    }
</style>



